@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  margin: 0;
  font-family: sans-serif;
  color: #252525;
}

button {
  font: inherit;
  cursor: pointer;
  background-color: #3c0080;
  border: 1px solid #3c0080;
  color: white;
  padding: 0.75rem 2rem; 
  border-radius: 6px;
  transition: background-color 0.3s, border-color 0.3s;

  &:hover,
  &:active {
    background-color: #5b14ac;
    border-color: #5b14ac;
  }
}
