/* styles.module.css */

.counter {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 2rem;
}

h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: white;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: bolder;
}

.value {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: white;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.buttonGroup {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
}

.inputGroup {
  margin-top: 1rem;
}

input[type='number'] {
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 0.5rem;
}

button {
  font: inherit;
  cursor: pointer;
  background-color: #3c0080;
  border: 1px solid #3c0080;
  color: white;
  padding: 0.75rem 2rem;
  border-radius: 6px;
  transition: background-color 0.3s, border-color 0.3s;
}

button:hover,
button:active {
  background-color: #5b14ac;
  border-color: #5b14ac;
}

.toggleButton {
  margin-bottom: 1rem;
}

.activatePremium {
  margin-top: 1rem;
  background-color: #2c6ea7;
  border-color: #2c6ea7;
}

.activatePremium:hover,
.activatePremium:active {
  background-color: #1a4d75;
  border-color: #1a4d75;
}
